<template>
    <v-btn @click.prevent="stripeCheckout" outlined class="px-16">
        Kreditkarte
    </v-btn>
</template>

<script>
import { loadStripe } from '@stripe/stripe-js';

export default {
    name: 'StripeCheckout',
    props: ['lineItems', 'hash'],
    methods: {
        async stripeCheckout() {
            const stripe = await loadStripe('pk_live_51Hb7OnD9k02yHazBoT7k6mWHehZ81iLVrXewhOifjoQKZOZ0FaIuuYAbrw9ZL5z0q92LhOZFH5qOinbDPVW2setK00uTKvqOBK');
            
            fetch(`/create-session/${this.hash}`, {
                    method: 'POST',
                    headers: { 'Content-Type' : 'application/json' },
                    body: JSON.stringify({ line_items: this.lineItems })
            })
            .then((res) => res.json())
            .then((session) => stripe.redirectToCheckout({ sessionId: session.id }))
            .then((res) => { if(res.error) console.error(res.error.message); }) // double catch? or catch ain't res.error? convolved
            .catch((err) => console.error(`Error: ${err}`));
        }
    },
    created() {
    }
}
</script>


