<template>
    <div ref="paypal"></div>
</template>

<script>
export default {
    name: 'PaypalCheckout',
    props: ['purchaseUnits', 'hash'],

    data() {
        return {
            loaded: false,
            // paidFor: false,
            price: null
        };
    },

    mounted() {
        const script = document.createElement('script');
        script.src = 'https://www.paypal.com/sdk/js?client-id=ASI8gtWWtaLxdNb-d3AXWcHlN9n9gQHIVDdsXLB5_GT823gRnSXV6SSY292ShwYf9EHwUdJmr7zkVkr5&currency=EUR&disable-funding=giropay,credit,card,sepa,sofort';
        script.addEventListener('load', this.setLoaded);
        document.body.appendChild(script);
    },

    methods: {
        setLoaded() {
            this.loaded = true;
            window.paypal.Buttons({
                style: { color: 'white' },

                createOrder: async (data, actions) => {
                    let finalPrice;

                    await fetch(`/price/${this.purchaseUnits.quantity}${this.purchaseUnits.ci ? '?ci=' + this.purchaseUnits.ci : ''}`)
                        .then((res) => res.json())
                        .then(parsed => {
                            console.log(parsed);
                            finalPrice = parsed.price
                            setTimeout(2000, () => console.log('>> chill 2s'));
                        })
                        .catch((err) => console.error(err));
                    
                    const purchaseObject = { purchase_units: [{ amount: { value: finalPrice } }] };

                    console.log(data, actions, purchaseObject);

                    await fetch('/paypal-check', {
                        method: 'POST',
                        headers: { 'Content-Type' : 'application/json' },
                        body: JSON.stringify({ data: purchaseObject })
                    })
                        .then((res) => res.json())
                        .catch(err => console.log(err));
                

                    return actions.order.create(purchaseObject);
                },
                
                onApprove: (data, actions) => {
                    return actions.order.capture().then(() => window.location = `https://www.pflanz-mit.de/success/${this.hash}`); // !!! REVERTE THESE URIs !!!
                },
                
                onCancel: () => window.location = `https://www.pflanz-mit.de/cancel/${this.hash}`,
                onError: () => window.location = `https://www.pflanz-mit.de/cancel/${this.hash}`
            })
            .render(this.$refs.paypal);
        }
    }
};
</script>