<template>

  <v-app>

    <NavBar></NavBar>

    <v-main>

      <v-img src="./assets/Baumspitzen.jpg" class="mb-12">
        <Landing></Landing>
      </v-img>

      <v-img src="./assets/Deutscher-Wald.jpg" class="mb-12">
        <Overview id="Uebersicht"></Overview>
      </v-img>

      <v-img src="./assets/Baeume-Draufsicht.jpg">
        <v-container fill-height class="d-flex justify-center">

          <v-row>

            <v-col cols=12 class="d-flex justify-center">
              <Preise id="Preise"></Preise>
            </v-col>

            <v-col class="d-flex" id="Kaufen">
              <Kauf ref="kauf"></Kauf>
            </v-col>

            <v-col class="d-flex flex-column">

              <Forms ref="person"></Forms>
              
              <v-card class="rounded-xl mt-6">
                <v-stepper v-model="progress">
                  <v-stepper-header>
                    <v-stepper-step
                      color="#0d7066"
                      :complete="progress > 1"
                      step="1"
                    >
                      Eingabe abschließen
                    </v-stepper-step>
                    <v-divider></v-divider>
                    <v-stepper-step
                      color="#0d7066"
                      :complete="progress > 2"
                      step="2"
                    >
                      Kauf abschließen
                    </v-stepper-step>
                  </v-stepper-header>
                  <v-stepper-items>
                    <v-stepper-content step="1">
                      <v-card justify="center" align="center">
                        <v-btn x-large color="#0d7066" class="white--text" width="50%" @click="checkPersonKauf">Zahlen</v-btn>
                      </v-card>
                    </v-stepper-content>
                    <v-stepper-content step="2">
                      <v-card justify="center" align="center">
                        <v-row>
                          <v-col>
                            <StripeCheckout :lineItems="line_items" :hash="hash"></StripeCheckout>
                          </v-col>

                          <v-col>
                            <PaypalCheckout :purchaseUnits="purchase_units" :hash="hash"></PaypalCheckout>
                          </v-col>
                        </v-row>
                      </v-card>
                    </v-stepper-content>
                  </v-stepper-items>
                </v-stepper>
              </v-card>

            </v-col>
          </v-row>
        </v-container>
      </v-img>

      <Footer></Footer>

      <v-overlay v-model="paymentCanceled"> <!-- there is a weird bug when this is active the paypal button disappears... its propably better to redirect to another page...-->
        <v-card color="white" align="center" class="rounded-xl py-10">
          <v-img :src="require('./assets/cross.png')" max-width="20%" contain/>
          <h1 class="black--text pb-4">Der Kauf wurde abgebrochen</h1>
          <v-btn @click="redirectToHome" color="red">Schliessen</v-btn>
        </v-card>
      </v-overlay>

      <v-overlay v-model="paymentSuccessful">
        <v-card color="white" align="center" class="rounded-xl py-10">
          <v-img :src="require('./assets/check.png')" max-width="20%" contain/>
          <h1 class="black--text pb-4">Der Kauf war erfolgreich!</h1>
          <p class="black--text">Vielen lieben Dank, dass Sie uns dabei unterstützen Bäume zu pflanzen.</p>
          <p class="black--text">Mit Ihrer Entscheidung, setzten sie ein klares Zeichen gegen Umweltbelastung und für nachhaltiges Schenken.</p>
          <v-btn @click="redirectToHome" color="red">Schliessen</v-btn>
        </v-card>
      </v-overlay>

    </v-main>

  </v-app>

</template>

<script>
import NavBar from './components/NavBar';
import Landing from './components/Landing';
import Overview from './components/Overview';
import Preise from './components/Preise';
import Kauf from './components/Kauf';
import Forms from './components/Forms';
import StripeCheckout from './components/StripeCheckout';
import PaypalCheckout from './components/PaypalCheckout';
import Footer from './components/Footer';
import baumPreiseID from './baumPreiseID';

export default {
  name: 'App',

  components: {
    NavBar,
    Landing,
    Overview,
    Preise,
    Kauf,
    Forms,
    StripeCheckout,
    PaypalCheckout,
    Footer
  },

  data() {
    return {
      line_items: [], // stripe
      purchase_units: {}, // paypal
      
      personObj: null,
      kaufObj: null,

      // overlays
      paymentCanceled: false,
      paymentSuccessful: false,
      
      progress: 1, // stepper
      
      hash: null
    };
  },

  created() {
    const flag = window.location.href;
    
    if(flag.includes('cancel')) {
      this.paymentCanceled = true;
    } else if(flag.includes('success')) {
      this.paymentSuccessful = true;
    }
  },

  methods: {
    redirectToHome() {
      window.location = 'https://www.pflanz-mit.de/';
    },

    async checkPersonKauf() {
      if(!this.$refs.person.submit()) {
        this.personObj = this.$refs.person.content();
      }

      if(this.$refs.kauf.submit()) {
        this.kaufObj = {
          numTrees: this.$refs.kauf.numTrees,
          price: this.$refs.kauf.sum(), // so i can easyly sum up total later
          choosenCorpIDs: this.$refs.kauf.choosenCorpIDs
        }
      }

      if(this.personObj && this.kaufObj) {
        // advance stepper
        this.progress = 2;


        // create db entry
        await fetch('/create-customer', {
          method: 'POST',
          headers: { 'Content-type' : 'application/json' },
          body: JSON.stringify({
            person: this.personObj,
            kauf: this.kaufObj
          })
        })
          .then((res) => res.json())
          .then(data => {
            console.log(data);
            this.hash = data.hash;
          }) // gangsta
          .catch((err) => console.error(err));


        // populate line_items for stripe
        this.line_items.push({
          price: baumPreiseID(this.kaufObj.numTrees),
          quantity: this.kaufObj.numTrees
        });

        if(this.kaufObj.choosenCorpIDs.length > 0) {
          this.line_items.push({
            price: 'price_1HgvjfD9k02yHazBJ0SivgF1',
            quantity: this.kaufObj.choosenCorpIDs.length
          });
        }

        console.log(this.line_items);


        // populate purchase_units for paypal
        this.purchase_units.quantity = this.kaufObj.numTrees;

        if(this.kaufObj.choosenCorpIDs.length > 0) {
          this.purchase_units.ci = this.kaufObj.choosenCorpIDs.length
        }

        console.log(this.purchase_units);

      }
    }
  }
};
</script>