<template>
    <div class="circle white--text d-flex flex-column">
        <countTo :startVal="start" :endVal="end" :duration="dur" separator=""></countTo>
        <p class="txt">Bäume wurden schon<p>
        <p class="txt mt-n8">gepflanzt!</p>
    </div>
</template>

<script>
import countTo from 'vue-count-to';

export default {
    name: 'TreeBubble',
    components: { countTo },
    data() {
        return {
            start: 0,
            end: null,
            dur: 8000
        }
    },
    async beforeCreate() {
        await fetch('/total-trees')
            .then(res => res.json())
            .then(data => this.end = Number(data.numTrees))
            // .then(() => console.log(this.end))
            .catch((err) => console.error(err));
    }
}
</script>

<style>
.circle {
    background-color: #0D7066;
    border-radius: 50%;
    margin: 40px;
    display: flex;

    animation-name: grow;
    animation-duration: 10s;
    animation-fill-mode: both;
    animation-delay: 2s;

    justify-content: center;
    align-items: center;
}

.txt {
    animation-name: grow-txt;
    animation-duration: 10s;
    animation-fill-mode: both;
    animation-delay: 2s;

    text-align: center;
}

@keyframes grow {
    from { height: 100px;  width: 100px;  font-size: 1rem; }
    to   { height: 400px; width: 400px; font-size: 8rem; }
}

@keyframes grow-txt {
    from { font-size: 0.1rem; }
    to   { font-size: 1.8rem; }
}
</style>
